$mobile-size: 750px;

header#header-pc {
    * {
        font-family: "SCDream7";
    }

    ul li a {
        color: unset;
        text-decoration: unset;
    }
}

header#header-pc.float {
    box-shadow: 0 2px 10px #0000001a;
    background: #ffffffcc;
    padding-top: 0px;
    transition: all 0.3s linear;

    img {
        transition: all 0.3s linear;
    }

    ul li {
        color: #003765;
        transition: all 0.3s linear;
    }
}

@keyframes fade {
    from {
        opacity: 0.8;
    }
    to {
        opacity: 1;
    }
}

@media screen and (max-width: $mobile-size) {
    header#header-pc {
        display: none;
    }
}
